// scene.fog = fog



const geometry = new THREE.DodecahedronBufferGeometry(0.6, 3);

const material = new THREE.MeshStandardMaterial({color: '#0000CC',  metalness: 0.8,
    roughness: 0.5});

const torusMesh = new THREE.InstancedMesh(geometry, material, 100);
scene.add(torusMesh);

for(let i = 0; i < 100; i++)
{
    const angle = Math.random() * Math.PI * 2 // Random angle
    const radius = 11 + Math.random() * 6      // Random radius
    const x = Math.cos(angle) * radius        // Get the x position using cosinus
    const z = Math.sin(angle) * radius        // Get the z position using sinus

    const position = new THREE.Vector3(
        x,
        Math.random() * 20,
       z
    )
    
  
    const quaternion = new THREE.Quaternion();
    quaternion.setFromEuler(new THREE.Euler((Math.random() - 0.5) * Math.PI * 2, (Math.random() - 0.5) * Math.PI * 2, 0));

    const matrix = new THREE.Matrix4();
     matrix.makeRotationFromQuaternion(quaternion);
    matrix.setPosition(position);
    

    torusMesh.setMatrixAt(i, matrix);
}



