// window Sizes
var sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}

window.addEventListener('resize', function resize()
{
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
})


// too much code for fullscreen!!!!
window.addEventListener('dblclick', function blclick ()
{
    const fullscreenElement = document.fullscreenElement || document.webkitFullscreenElement

    if(!fullscreenElement)
    {
        if(canvas.requestFullscreen)
        {
            canvas.requestFullscreen()
        }
        else if(canvas.webkitRequestFullscreen)
        {
            canvas.webkitRequestFullscreen()
        }
    }
    else
    {
        if(document.exitFullscreen)
        {
            document.exitFullscreen()
        }
        else if(document.webkitExitFullscreen)
        {
            document.webkitExitFullscreen()
        }
    }
})


//Camera
// Base camera
const camera = new THREE.PerspectiveCamera(75, sizes.width / sizes.height, 0.1, 100);
camera.position.set(-6, 4, 7);
scene.add(camera);

// Orbit Controls
const controls = new OrbitControls(camera, canvas);
controls.target.set(0, 1, 0);
controls.enableDamping = true;

// Renderer
 var renderer = new THREE.WebGLRenderer({
    canvas: canvas
})
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))  // optimais the look of the pixel
renderer.setClearColor('#290030')  //
renderer.physicallyCorrectLights = true
renderer.outputEncoding = THREE.sRGBEncoding

const clock = new THREE.Clock()
var previousElapsedTime = 0

function upDate ()
{
   stats.begin()

    var elapsedTime = clock.getElapsedTime()
    var deltaTime = elapsedTime - previousElapsedTime
    previousElapsedTime = elapsedTime

   //real time for the model animation
    if(mixer !== null){
    mixer.update(deltaTime)
    }

    // point light moving
    var pointOneAngle = elapsedTime * 0.5
    pointOne.position.x = Math.cos(pointOneAngle) * 7
    pointOne.position.z = Math.sin(elapsedTime * 2)
    pointOne.position.y = 9

    var pointTwoAngle = - elapsedTime * 0.32
    pointTwo.position.x = Math.cos(pointTwoAngle) * 5
    pointTwo.position.z = Math.sin(pointTwoAngle) * -7
    pointTwo.position.y = 4

    var pointThreeAngle = - elapsedTime * 0.8
    pointThree.position.x = Math.sin(pointThreeAngle) * (7 + Math.sin(elapsedTime * 0.2))
    pointThree.position.z = Math.cos(pointThreeAngle) * (7 + Math.sin(elapsedTime * 0.2))
    pointThree.position.y = 2

    var directionalLightAngle = elapsedTime * 0.2
    directionalLight.position.x = Math.sin(directionalLightAngle) * 0.5
    directionalLight.position.z = Math.cos(directionalLightAngle) * 0.5
    directionalLight.position.y = 5

    // Update controls
    controls.update()

    // Render
    renderer.render(scene, camera)

    // Call fanction upDate again on the next frame
    window.requestAnimationFrame(upDate)

    stats.end()
}

upDate()


